@include font('Cambria', 'Cambria');
@include font('ProximaNova-Regular', 'ProximaNova-Regular');
@include font('ProximaNova-Bold', 'ProximaNova-Bold');

// fonts
$default-fonts: Arial, sans-serif;
@mixin cambria {
	font-family: 'Cambria', $default-fonts; }
@mixin nova-regular {
	font-family: 'ProximaNova-Regular', $default-fonts; }
@mixin nova-bold {
	font-family: 'ProximaNova-Bold', $default-fonts; }

// colors
// $black: #000

@mixin trs {
	transition: all 0.25s; }

@mixin smooth {
	-webkit-font-smoothing: antialiased; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	display: flex;
	flex-direction: column;
	position: relative;
	min-width: 320px;
	@include nova-regular;
	font-size: 12px;
	color: #464646;
	line-height: 1.5;
	letter-spacing: 0.02em;
	&.is-locked {
		overflow: hidden; } }

.wrapper {
	position: relative;
	margin: 0 auto;
	flex: 1 0 auto;
	width: 100%; }

.container {
	margin: 0 auto;
	padding: 0 30px;
	max-width: 1230px;
	@include pie-clearfix;
	@include r(1023) {
		padding-left: 15px;
		padding-right: 15px; }
	&__row {
		margin-left: -15px;
		margin-right: -15px;
		@include r(479) {
			padding-top: 15px; } } }

/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

* {
	@include selection {
		color: #fff;
		background-color: #15308c; } }

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal; }

/* ===================================
	 Header
	 @mixin ================================== */

/* Hamburger
	----------------------------------- */
.hamburger {
	display: none;
	position: absolute;
	top: 0;
	right: 10px;
	z-index: 102;
	padding: 5px;
	pointer-events: auto;
	-webkit-tap-highlight-color: transparent;
	cursor: pointer;
	transition-property: opacity, -webkit-filter;
	transition-property: opacity, filter;
	transition-property: opacity, filter, -webkit-filter;
	transition-duration: 0.15s;
	transition-timing-function: linear;
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	overflow: visible;
	transition: all 0.5s;
	@include r(767) {
		display: block; }
	@include r(424) {
		margin-left: 8px; }
	&__box {
		width: 23px;
		height: 16px;
		display: block;
		position: relative; }
	&__inner {
		display: block;
		top: 50%;
		margin-top: -2px;
		width: 23px;
		height: 3px;
		background-color: #15308c;
		position: absolute;
		transition-property: -webkit-transform;
		transition-property: transform;
		transition-property: transform, -webkit-transform;
		transition-duration: 0.15s;
		transition-timing-function: ease;
		&::before, &::after {
			width: 23px;
			height: 3px;
			background-color: #15308c;
			position: absolute;
			transition-property: -webkit-transform;
			transition-property: transform;
			transition-property: transform, -webkit-transform;
			transition-duration: 0.15s;
			transition-timing-function: ease; }
		&::before, &::after {
			content: "";
			display: block; }
		&::before {
			top: -7px; }
		&::after {
			bottom: -7px; } }
	&.is-active {
		top: 10px; }
	&.is-active &__inner {
		background-color: #fff; }
	&.is-active &__inner:before,
	&.is-active &__inner:after {
		background-color: #fff; }
	&_spin &__inner {
		transition-duration: 0.3s;
		transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
		&::before {
			transition: top 0.1s 0.34s ease-in, opacity 0.1s ease-in; }
		&::after {
			transition: bottom 0.1s 0.34s ease-in, -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
			transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
			transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19), -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19); } }
	&.is-active &__inner {
		-webkit-transform: rotate(225deg);
		transform: rotate(225deg);
		transition-delay: 0.14s;
		transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
		&::before {
			top: 0;
			opacity: 0;
			transition: top 0.1s ease-out, opacity 0.1s 0.14s ease-out; }
		&::after {
			bottom: 0;
			-webkit-transform: rotate(-90deg);
			transform: rotate(-90deg);
			transition: bottom 0.1s ease-out, -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
			transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
			transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1); } } }

.header {
