				margin-left: 0;
				padding: 0 15px;
				width: 50%;
				text-align: right; }
			@include r(639) {
				margin-top: -20px;
				width: 55%; }
			@include r(424) {
				margin-top: -20px; } } }
	&__search {
		margin-top: 25px;
		@include r(1023) {
			margin-top: 15px; }
		@include r(639) {
			margin-top: 0; } }
	&__cart {
		margin-top: -5px; }
	&__form-cart {
		margin-left: 3px;
		margin-top: 7px;
		@include r(1023) {
			margin-top: 12px; }
		@include r(639) {
			margin-top: 15px; }
		@include r(424) {
			margin-top: 7px; }
		&-submit {
			position: relative;
			padding: 8px 15px;
			font-size: 14px;
			color: #15308c;
			line-height: 1;
			text-transform: uppercase;
			border: 3px solid #15308c;
			white-space: nowrap;
			@include trs;
			@include r(1023) {
				min-width: 65%; }
			@include r(639) {
				max-width: none;
				padding: 0 10px;
				height: 30px;
				font-size: 12px;
				line-height: 24px;
				border-width: 2px; }
			@include r(374) {
				width: 100%; }
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				left: 0;
				right: 0;
				transform: translate3d(0,-50%,0);
				z-index: -1;
				pointer-events: none;
				height: 0;
				opacity: 0;
				background-color: #15308c;
				@include trs; }
			&:hover {
				color: #ffffff;
				&:before {
					height: calc(100% - 2px);
					opacity: 1; } } } }
	&__phone {
		margin-top: 5px;
		font-size: 24px;
		color: #464545;
		line-height: 1.4;
		@include r(1023) {
			font-size: 30px; }
		@include r(639) {
			font-size: 18px; }
		@include r(424) {
			margin-top: 0;
			font-size: 16px;
			white-space: nowrap; } }
	&__recall {
		font-size: 14px;
		color: #15308c;
		text-decoration: underline;
		@include r(424) {
			white-space: nowrap;
			font-size: 12px; }
		&:hover {
			text-decoration: underline; } } }

.nav {
	display: flex;
	overflow: hidden;
	font-size: 18px;
	color: #ffffff;
	line-height: 45px;
	text-align: center;
	@include r(767) {
		flex-direction: column;
		opacity: 0;
		pointer-events: none;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 101;
		background-color: rgba(#000000, .85);
		justify-content: center;
		transition: none; }
	&.is-active {
		transition: all 0.5s;
		@include r(767) {
			pointer-events: auto;
			opacity: 1; } }
	&__link {
		position: relative;
		padding: 0 5px;
		flex: 1 0 auto;
		background-color: #15308c;
		@include trs;
		@include r(1023) {
			font-size: 15px;
			line-height: 40px; }
		@include r(767) {
			display: table;
			margin: 5px auto;
			opacity: 0;
			flex: 0 0 auto;
			font-size: 21px;
			line-height: 30px;
			background-color: transparent; }
		&.is-active {
			@include r(767) {
				position: relative;
				&:before {
					content: '';
					position: absolute;
					bottom: 0;
					left: 0;
					right: 0;
					z-index: 1;
					height: 1px;
					background-color: #ffffff; } } }
		& + & {
			border-left: 1px solid rgba(#fff, .2);
			@include r(767) {
				border-left: none; } }
		&:hover {
			color: #15308c;
			background-color: #f6f6f6; } }
	&.is-active &__link {
		transition: none;
		animation: anim-top 0.6s forwards;
		&:nth-child(3) {
			animation-delay: 0.15s; }
		&:nth-child(4) {
			animation-delay: 0.3s; }
		&:nth-child(5) {
			animation-delay: 0.45s; }
		&:nth-child(6) {
			animation-delay: 0.6s; }
		&:nth-child(7) {
			animation-delay: 0.75s; } } }

@keyframes anim-top {
	0% {
		opacity: 0;
		top: 50px; }
	100% {
		opacity: 1;
		top: -25px; } }

.cart {
	position: relative;
	@include r(639) {
		white-space: nowrap; }
	&__icon {
		margin-top: 5px;
		float: left;
		margin-right: 12px;
		font-size: 0;
		color: #d0d0d0;
		@include r(1023) {
			float: none;
			display: inline-block;
			vertical-align: 1px; }
		@include r(639) {
			margin-right: 0; }
		@include r(424) {
			vertical-align: bottom; }
		@include r(374) {
			margin-right: 0; }
		.icon {
			@include r(424) {
				font-size: 24px; } } }
	&__content {
		overflow: hidden;
		@include r(1023) {
