				height: 18px;
				font-size: 12px;
				color: #ffffff;
				text-align: center;
				line-height: 19px;
				background-color: #15308c;
				border-radius: 50%; }
			@include r(424) {
				top: -8px;
				left: -12px;
				min-width: 15px;
				height: 15px;
				line-height: 16px; }
			@include r(374); } } }

.search {
	position: relative;
	@include pie-clearfix;
	&__input {
		position: relative;
		padding: 0 10px 0 40px;
		width: 100%;
		height: 38px;
		@include nova-regular;
		font-size: 14px;
		line-height: 38px;
		background-color: #ffffff;
		border: 1px solid #d0d0d0;
