		@include trs;
		&:hover {
			color: #15308c; }
		.icon {
			@include r(639) {
				margin-top: 1px;
				font-size: 18px; } } } }

.nav-about {
	font-size: 14px;
	color: rgba(#464646, .7);
	white-space: nowrap;
	&__link {
		position: relative;
		white-space: nowrap;
		&:hover {
