		line-height: 1;
		@include r(1023) {
			margin-bottom: 20px; } }
	&__card-slider {
		float: left;
		width: 55%;
		@include r(767) {
			width: 45%; }
		@include r(539) {
			float: none;
			width: auto; } }
	&__slider-desc {
		margin-top: -5px;
		padding-left: 30px;
		float: left;
		width: 45%;
		@include r(767) {
			padding-left: 20px;
			width: 55%; }
		@include r(539) {
			margin-top: 20px;
			float: none;
			width: auto;
			padding-left: 0; } } }


/* Card-slider
	----------------------------------- */
.card-slider {
	&__for {
		visibility: hidden;
		&.slick-initialized {
			visibility: visible; }
		&-img {
			width: 100%; }
		&-item {
			position: relative; }
		&-info {
			position: absolute;
			z-index: 1;
			top: 0;
			right: 0;
			padding: 0 5px;
			font-size: 11px;
			color: #ffffff;
			height: 22px;
			line-height: 22px;
			&_blue {
				background-color: #15308c; } }
		&-prev {
			position: absolute;
			z-index: 3;
			top: 34%;
			left: 7px;
			height: 71px;
			width: 71px;
			font-size: 0;
			color: #15308c;
			text-indent: -5px;
			background-color: #f6f6f6;
			border-radius: 50%;
			@include trs;
			@include r(1229) {
				width: 40px;
				height: 40px;
				left: 5px; }
			@include r(767) {
				width: 24px;
				height: 24px;
				text-indent: 0; }
			.icon {
				@include r(1229) {
					font-size: 20px; }
				@include r(767) {
					font-size: 14px; } }
			&:hover {
				color: #ffffff; } }
		&-next {
			position: absolute;
			z-index: 3;
			top: 34%;
			right: 7px;
			height: 71px;
			width: 71px;
			font-size: 0;
			color: #15308c;
			text-indent: 5px;
			background-color: #f6f6f6;
			border-radius: 50%;
			@include trs;
			@include r(1229) {
				width: 40px;
				height: 40px;
				right: 5px; }
			@include r(767) {
				width: 24px;
				height: 24px;
				text-indent: 0; }
			.icon {
				@include r(1229) {
					font-size: 20px; }
				@include r(767) {
					font-size: 14px; } }
			&:hover {
				color: #ffffff; } }
		&-prev.slick-disabled,
		&-next.slick-disabled {
			display: none!important; } }
	&__nav {
		margin-top: 13px;
		margin-left: -15px;
		margin-right: -15px;
		visibility: hidden;
		&.slick-initialized {
			visibility: visible; }
		@include r(1023) {
			margin-left: -5px;
			margin-right: -5px; }
		&-item {
			position: relative;
			cursor: pointer;
			margin: 0 15px;
			@include r(1023) {
				margin: 0 5px; }
			&:before {
				content: '';
				position: absolute;
				z-index: 2;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: rgba(#000000, .35);
				@include trs; }
			&.slick-current,
			&:hover {
				&:before {
					background-color: transparent; } } }
		&-img {
			width: 100%; } } }

/* Slider-desc
	----------------------------------- */
.slider-desc {
	font-size: 14px;
	line-height: 1.4;
	&__field {
		@include pie-clearfix;
		& + & {
			margin-top: 25px;
			@include r(767) {
				margin-top: 15px; }
			@include r(539) {
				margin-top: 10px; } }
		& + &_price {
			margin-top: 30px;
			@include r(767) {
				margin-top: 20px; }
			@include r(539) {
				margin-top: 10px; } }
		&_price + & {
			margin-top: 15px;
			@include r(767) {
				margin-top: 10px; }
			@include r(539) {
				margin-top: 5px; } }
		& + &_phone {
			margin-top: 45px;
			@include r(767) {
				margin-top: 30px; }
			@include r(539) {
				margin-top: 15px; } } }
	&__label {
		color: #aeaeae;
		&-text {
			color: #454545; } }
	&__field_half &__label {
		margin-bottom: 5px; }
	&__custom-select {
		cursor: pointer;
		position: relative;
		margin-top: 7px;
		display: table;
		@include r(767) {
			margin-top: 2px; }
		&:before {
			content: '';
			position: absolute;
			z-index: 2;
			cursor: pointer;
			right: 8px;
			top: 11px;
			@include s(menu-arrow);
			transform: rotate(90deg); }
		&:after {
			position: absolute;
			z-index: 1;
			top: 1px;
			left: 1px;
			bottom: 1px;
			width: 35px;
			background-color: #ffffff; } }
	&__select {
		cursor: pointer;
		padding: 2px 40px 3px 5px;
		font-size: 16px;
		min-width: 100%;
		color: #4c4c4c;
		line-height: 1.5;
		background-color: transparent;
		border-radius: 0;
		border: 1px solid #b0b0b0;
		appearance: none;
		&:-moz-focusring {
			color: transparent;
			text-shadow: 0 0 0 #000; } }
	&__price {
		@include nova-bold;
		font-size: 32px;
		color: #15308c;
		&-currency {
			display: inline-block;
			vertical-align: -1px;
			@include s(currency-rub); } }
	&__submit {
		position: relative;
		padding: 8px 15px;
		font-size: 14px;
		color: #15308c;
		line-height: 1;
		text-transform: uppercase;
		border: 3px solid #15308c;
		white-space: nowrap;
		@include trs;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			right: 0;
			transform: translate3d(0,-50%,0);
			z-index: -1;
			height: 0;
			opacity: 0;
			background-color: #15308c;
			@include trs; }
		&:hover {
			color: #ffffff;
			&:before {
				height: calc(100% - 2px);
				opacity: 1; } } }
	&__number-control {
		font-size: 14px;
		color: #15308c;
		height: 30px;
		width: 40px;
		display: inline-block;
		vertical-align: middle;
		border-top: 2px solid #b0b0b0;
		border-bottom: 2px solid #b0b0b0;
		@include trs; }
	.input-group {
		margin-top: 3px;
		margin-right: 20px;
		float: left;
		height: 30px;
		line-height: 30px;
		font-size: 0;
		&-btn {
			cursor: pointer;
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 20px;
			height: 30px;
			color: #ffffff;
			background-color: #b0b0b0;
			@include trs;
			&:nth-child(1) .btn,
			&:nth-child(3) .btn {
				display: block;
				width: 100%;
				height: 100%;
				text-indent: -9999px;
				overflow: hidden;
				&:before {
					content: '';
					pointer-events: none;
					@include vcenter;
					z-index: 1;
					@include s(number-arrow); } }
			&:nth-child(3) .btn {
				&:before {
					transform: translate3d(-50%,-50%,0) rotate(180deg); } } } }
	.input-group:hover input {
		border-color: #15308c; }
	.input-group:hover {
		.input-group-btn {
			background-color: #15308c; } }
	&__phone {
		font-size: 24px;
		color: #454545;
		@include r(767) {
			font-size: 21px; } } }

/* Card-about
