/* Card
	----------------------------------- */
.card {
	display: flex;
	flex-direction: column;
	padding: 6px 7px 0;
	width: 252px;
	text-align: center;
	border: 1px solid #e1e1e1;
	&__in {
		flex: 1 0 auto; }
	&__image {
		position: relative;
		display: block;
		&-hit {
			content: '';
			position: absolute;
			z-index: 1;
			top: -3px;
			left: -4px;
			@include s(card-hit); } }
	&__title {
		margin-top: 5px;
		font-size: 16px;
		color: #313131;
		text-transform: uppercase; }
	&__label {
		margin-top: 4px;
		font-size: 14px;
		color: #929292;
		&-color {
			color: #454545; } }
	&__row {
		margin-top: 20px;
		@include pie-clearfix; }
	&__price {
		float: left;
		margin-top: 2px;
		width: 50%;
		@include nova-bold;
		font-size: 24px;
		color: #15308c;
		text-align: left;
		padding-left: 3px;
		&-currency {
			display: inline-block;
			margin-left: -2px;
			vertical-align: -4px;
			@include s(currency-rub);
			transform: scale(0.76); } }
	&__btn {
		float: right;
		width: 50%;
		position: relative;
		padding: 8px 15px;
		font-size: 14px;
		color: #15308c;
		line-height: 1;
		text-transform: uppercase;
		border: 3px solid #15308c;
		white-space: nowrap;
		@include trs;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			right: 0;
			transform: translateY(-50%);
			z-index: -1;
			height: 0;
			opacity: 0;
			background-color: #15308c;
			@include trs; }
		&:hover {
			color: #ffffff;
			&:before {
				height: calc(100% - 2px);
				opacity: 1; } } }
	&__more {
		display: block;
		margin-top: 16px;
		margin-left: -7px;
		margin-right: -7px;
		font-size: 14px;
		color: #969696;
		line-height: 27px;
		background-color: #f3f3f3;
