$icons: (0:0);
$icons: map-merge($icons,(akticom: (X: -65px, Y:0px, W: 100px, H: 35px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(card-hit: (X: 0px, Y:0px, W: 61px, H: 61px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(currency-rub: (X: 0px, Y:-65px, W: 20px, H: 25px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(lang-1: (X: -92px, Y:-39px, W: 22px, H: 15px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(lang-2: (X: -65px, Y:-39px, W: 23px, H: 15px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(menu-arrow: (X: -24px, Y:-65px, W: 6px, H: 11px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(number-arrow: (X: -118px, Y:-39px, W: 12px, H: 7px, TW: 165px, TH: 90px, IMG: '../img/icons.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
	$icon: map-get($icons, $icon);
	@return map-get($icon, $attr); }

@mixin sprite($iconName) {
	background-image: url(icon-attr($iconName, IMG));
	width: icon-attr($iconName, W);
	height: icon-attr($iconName, H);
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
	background-image: url(icon-attr($iconName, IMG));
	$width: icon-attr($iconName, W);
	$height: icon-attr($iconName, H);
	width: $width/2;
	height: $height/2;
	$x: icon-attr($iconName, X);
	$y: icon-attr($iconName, Y);
	background-position: $x/2 $y/2;
	$tw: icon-attr($iconName, TW);
	$th: icon-attr($iconName, TH);
	background-size: $tw/2 $th/2; }
@mixin sprite-position-retina($iconName) {
	$x: icon-attr($iconName, X);
	$y: icon-attr($iconName, Y);
	background-position: $x/2 $y/2; }


@mixin s($i) {
	@include sprite($i); }
@mixin sp($i) {
	@include sprite-position($i); }
@mixin sr($i) {
	@include sprite-retina($i); }
@mixin spr($i) {
	@include sprite-position-retina($i); }

