/* Hamburger ----------------------------------- */
.hamburger { display: none; position: absolute; top: 0; right: 10px; z-index: 102; padding: 5px; pointer-events: auto; -webkit-tap-highlight-color: transparent; cursor: pointer; transition-property: opacity, -webkit-filter; transition-property: opacity, filter; transition-property: opacity, filter, -webkit-filter; transition-duration: 0.15s; transition-timing-function: linear; font: inherit; color: inherit; text-transform: none; background-color: transparent; border: 0; overflow: visible; transition: all 0.5s; }

@media only screen and (max-width: 767px) { .hamburger { display: block; } }

@media only screen and (max-width: 424px) { .hamburger { margin-left: 8px; } }

.hamburger__box { width: 23px; height: 16px; display: block; position: relative; }

.hamburger__inner { display: block; top: 50%; margin-top: -2px; width: 23px; height: 3px; background-color: #15308c; position: absolute; transition-property: -webkit-transform; transition-property: transform; transition-property: transform, -webkit-transform; transition-duration: 0.15s; transition-timing-function: ease; }

.hamburger__inner::before, .hamburger__inner::after { width: 23px; height: 3px; background-color: #15308c; position: absolute; transition-property: -webkit-transform; transition-property: transform; transition-property: transform, -webkit-transform; transition-duration: 0.15s; transition-timing-function: ease; }

.hamburger__inner::before, .hamburger__inner::after { content: ""; display: block; }

.hamburger__inner::before { top: -7px; }

.hamburger__inner::after { bottom: -7px; }

.hamburger.is-active { top: 10px; }

.hamburger.is-active .hamburger__inner { background-color: #fff; }

.hamburger.is-active .hamburger__inner:before, .hamburger.is-active .hamburger__inner:after { background-color: #fff; }

.hamburger_spin .hamburger__inner { transition-duration: 0.3s; transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }

.hamburger_spin .hamburger__inner::before { transition: top 0.1s 0.34s ease-in, opacity 0.1s ease-in; }

.hamburger_spin .hamburger__inner::after { transition: bottom 0.1s 0.34s ease-in, -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19); transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19); transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19), -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19); }

.hamburger.is-active .hamburger__inner { -webkit-transform: rotate(225deg); transform: rotate(225deg); transition-delay: 0.14s; transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

.hamburger.is-active .hamburger__inner::before { top: 0; opacity: 0; transition: top 0.1s ease-out, opacity 0.1s 0.14s ease-out; }

.hamburger.is-active .hamburger__inner::after { bottom: 0; -webkit-transform: rotate(-90deg); transform: rotate(-90deg); transition: bottom 0.1s ease-out, -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1); transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1); transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1); }

